@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #181515;
  font-family: 'Roboto', sans-serif;
}
.tic-tac-toe {
    display: flex;
    flex-wrap: wrap;
    margin: 30px auto 0;
    width: 300px;
    height: 300px;
    background-color: #61dafb;
    border: 10px solid #28abcf;
    border-radius: 15px;
    cursor: pointer;
}
.square {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 93px;
    height: 94px;
    border: 3px solid #28abcf;
    color:  #28abcf;
    font-size: 50px;
}

.score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    height: 80px;
    font-size: 30px;
    margin: 30px auto 0;
    padding: 0 10px;
    background-color: #61dafb;
    color:  #28abcf;
    border: 10px solid #28abcf;
    border-radius: 15px;
}

